.rating-line {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(15deg, #d20b54, #ffb84a);
  -webkit-clip-path: polygon(100% 0, 0 0, 100% 100%);
  clip-path: polygon(100% 0, 0 0, 100% 100%);
  overflow: hidden;

  &:after {
    content: '';
    position: absolute;
    top: 50px;
    right: 0;
    background: url("../images/rating-line.svg") no-repeat;
    background-size: 40px 100%;
    width: 40px;
    height: 100%;
    opacity: .5;
  }

  &_alt {

    &:after {
      top: 20px;
    }

  }

}

.rating-line-wrap {
  flex: 0 0 194px;
  padding-bottom: 45px;
  background-color: rgba(204, 204, 204, 0.1);

  &_pb-0 {
    padding-bottom: 0;
  }

  &_w-bg {
    flex: 0 0 125px;
    background-color: transparent;
  }

  &_tdn {

  }

}

.rating-detail {
  display: flex;
  flex: 1 1 274px;
  padding-top: 47px;

  &__label-box {
    flex: 1 1 77px;
  }

  &__label-group {

    & ul {
      margin: 0;
      padding: 50px 0 97px 16px;
      list-style: none;

      & li {
        margin-bottom: 40px;
        font-family: $font-family;
        font-size: 18px;
        font-weight: 900;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #000000;

        &:last-child {
          margin-bottom: 0;
        }

      }

    }

  }

  &__title-box {
    flex-grow: 1;
    padding-left: 20px;
  }

  &__title-group {
    padding-bottom: 50px;

    & .y1 {
      color: #f9c856;
    }

    & .y2 {
      color: #f9a842;
    }

    & .y3 {
      color: #f77d27;
    }

    & h3 {
      margin: 0;
      font-family: $font-family;
      font-size: 36px;
      font-weight: 400;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 5px;
      text-align: left;
      color: #f9c856;
      text-transform: uppercase;
    }

    & ul {
      margin: 0;
      padding: 5px 0 0 0;
      list-style: none;

      & li {
        margin-bottom: 24px;
        font-family: $font-family;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.21;
        letter-spacing: normal;
        text-align: left;
        color: #000000;

        &:last-child {
          margin-bottom: 0;
        }

      }

    }

  }

}

.rating-scroll {
  overflow: auto;
}

.rating {
  display: flex;
  position: relative;
  padding-top: 35px;
  min-width: 931px;
}

.rating-label {
  padding: 20px 20px 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__item {
    font-family: $font-family;
    font-size: 24px;
    font-weight: 900;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #ffb54a;
    padding-bottom: 60px;

    &.y1 {
      color: #ffb54a;
    }

    &.y2 {
      color: #f89d4c;
    }

    &.y3 {
      color: #f1814d;
    }

    &.y4 {
      color: #e9664e;
    }

    &.y5 {
      color: #e24b50;
    }

    &.y6 {
      color: #db2f52;
    }

  }

}

.rating-list-wrap {
  flex: 1 1 auto;
  padding-left: 50px;
}

.rating-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
  //margin: 0 -18px;

  &__item {
    width: 33.33%;
    //padding: 0 18px 36px;
  }

}

.rating-list-profile {
  display: block;
  padding: 20px;
  font-family: $font-family;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #ffb54a;
  text-decoration: none;
  transition: all .2s ease-in-out;

  &.y1 {
    color: #ffb54a;
  }

  &.y2 {
    color: #f89d4c;
  }

  &.y3 {
    color: #f1814d;
  }

  &.y4 {
    color: #e9664e;
  }

  &.y5 {
    color: #e24b50;
  }

  &.y6 {
    color: #db2f52;
  }

  & span {
    display: block;
  }

  & span:first-child {
    font-weight: 700;
  }

  &:hover {
    border-radius: 8px;
    background-color: #ffffff;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.13);
  }


}

.rating-modal {
  display: none;
  position: absolute;
  top: 270px;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 760px;
  padding: 50px 20px 50px 60px;
  background-color: #ffffff;
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.2);

  &_show {
    display: block;
    animation: mmslideIn .2s cubic-bezier(0,0,.2,1);
  }

  &__head {
    display: flex;
    align-items: center;
  }

  &__close {
    width: 28px;
    height: 28px;
    background: url("../images/close.svg") no-repeat;
    border: 0;
    outline: none;
    cursor: pointer;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .5;
    }

  }

  &__title {
    padding-left: 33px;
    font-family: $font-family;
    font-size: 36px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #000000;
  }

  &__np-btn {
    margin-left: auto;
  }

  &__next {
    position: relative;
    padding-left: 25px;
    font-family: $font-family;
    font-size: 14px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: right;
    color: #000000;
    background: transparent;
    border: 0;
    outline: none;
    cursor: pointer;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .7;
    }

    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 5px;
      font-size: 18px;
      color: #000000;
    }

  }

  &__previous {
    position: relative;
    padding-left: 15px;
    padding-right: 25px;
    font-family: $font-family;
    font-size: 14px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.14;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
    background: transparent;
    border: 0;
    outline: none;
    cursor: pointer;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .5;
    }

    &:before {
      content: '/';
      position: absolute;
      left: 0;
      top: 5px;
      font-size: 18px;
      color: #000000;
    }

    &:after {
      content: '';
      position: absolute;
      right: 0;
      top: 5px;
      font-size: 18px;
      color: #000000;
    }

  }

  &__body {
    display: flex;
    padding-top: 55px;
  }

  &__img-wrap {
    width: 40%;
  }

  &__img {
    width: 100%;
  }

  &__label-wrap {
    display: flex;
    align-items: center;
  }

  &__info {
    width: 60%;
    padding-left: 50px;
  }

  &__label {
    padding: 6px 23px 8px;
    font-family: $font-family;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #d68900;
    text-transform: uppercase;
    border-radius: 18px;
    background-color: #ffd580;
  }

  &__label-question {
    margin-left: 7px;
    width: 16px;
    height: 16px;
    opacity: 0.1;
    background-color: #000000;
    font-family: $font-family;
    font-size: 12px;
    font-weight: 400;
    text-align: center;
    line-height: 1.4;
    color: #fff;
    border-radius: 8px;
    text-decoration: none;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: 0.5;
    }

  }

  &__name {
    margin: 16px 0 0 0;
    font-family: $font-family;
    font-size: 36px!important;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
  }

  &__date {
    margin: 10px 0 0 0;
    font-family: $font-family;
    font-size: 14px!important;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
  }

  &__position {
    margin: 10px 0 0 0;
    font-family: $font-family;
    font-size: 18px!important;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
  }

  &__social {
    margin-top: 40px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .rating-line-wrap {
    padding-bottom: 45px;

    &_tdn {
      display: none;
    }

  }

  .rating-list-wrap {
    flex: 1 1 auto;
    padding-left: 30px;
  }

  .rating-list-profile {
    padding: 15px;
  }

  .rating-modal {
    width: 650px;
  }

}

